<template>
  <el-select
    v-model="serviceValue"
    placeholder="勤务中心"
    @change="handleServicesChange"
  >
    <el-option
      v-for="item in serviceList"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    >
    </el-option>
  </el-select>
</template>

<script>
export default {
  data() {
    return {
      serviceValue: "勤务中心",
      serviceList: [
        {
          value: "ts",
          label: "勤务态势",
        },
        {
          value: "zy",
          label: "勤务资源",
        },
        {
          value: "bb",
          label: "勤务报备",
        },
        {
          value: "fx",
          label: "统计分析",
        },
        {
          value: "jx",
          label: "绩效考核",
        },
      ],
    };
  },
  methods: {
    handleServicesChange(item) {
      if (this.$route.path.includes(item)) {
        this.serviceValue = "勤务中心";

        return this.$message.warning("已在当前页");
      }
      switch (item) {
        case "ts":
          this.$router.push("/nav/service-ts");
          break;
        case "zy":
          this.$router.push("/nav/service-zy");
          break;
        case "bb":
          this.$router.push("/nav/service-bb");
          break;
        case "fx":
          this.$router.push("/nav/service-fx");
          break;
        case "jx":
          this.$router.push("/nav/service-jx");
          break;
      }
      this.serviceValue = "";
    },
  },
};
</script>

