<template>
  <el-select
    v-model="guideValue"
    placeholder="指挥中心"
    @change="handleGuidesChange"
  >
    <el-option
      v-for="item in guideList"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    >
    </el-option>
  </el-select>
</template>

<script>
export default {
  data() {
    return {
      guideValue: "指挥中心",
      guideList: [
        {
          value: "zh",
          label: "指挥一张图",
        },
        {
          value: "zx",
          label: "坐席监控",
        },
        {
          value: "jq",
          label: "警情查询",
        },
        {
          value: "tj",
          label: "统计分析",
        },
        {
          value: "ya",
          label: "预案管理",
        },
        {
          value: "xt",
          label: "协同会商",
        },
      ],
    };
  },
  methods: {
    handleGuidesChange(item) {
      if (this.$route.path.includes(item)) {
        this.guideValue = "指挥中心";
        return this.$message.warning("已在当前页");
      }

      switch (item) {
        case "zh":
          this.$router.push("/nav/guides-zh");
          break;
        case "zx":
          this.$router.push("/nav/guides-zx");
          break;
        case "jq":
          this.$router.push("/nav/guides-jq");
          break;
        case "tj":
          this.$router.push("/nav/guides-tj");
          break;
        case "ya":
          this.$router.push("/nav/guides-ya");
          break;
        case "xt":
          this.$router.push("/nav/guides-xt");
          break;
      }
      this.guideValue = "";
    },
  },
};
</script>

