<template>
  <el-select
    v-model="newsValue"
    placeholder="情报中心"
    @change="handleNewsChange"
  >
    <el-option
      v-for="item in newsList"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    >
    </el-option>
  </el-select>
</template>

<script>
export default {
  data() {
    return {
      newsValue: "情报中心",
      newsList: [
        {
          value: "ts",
          router: "/nav/news-ts",
          label: "态势分析",
        },
        {
          value: "sj",
          label: "数据共享",
        },
        {
          value: "jh",
          label: "聚合搜索",
        },
        {
          value: "fx",
          label: "分析研判",
        },
        {
          value: "ry",
          label: "人员管控",
        },
        {
          value: "aj",
          label: "案件侦办",
        },
        {
          value: "sc",
          label: "手采应用",
        },
        {
          value: "dw",
          label: "多维统计",
        },
      ],
    };
  },
  methods: {
    handleNewsChange(item) {
      if (this.$route.path.includes(item)) {
        this.newsValue = "情报中心";

        return this.$message.warning("已在当前页");
      }
      switch (item) {
        case "ts":
          this.$router.push("/nav/news-ts?headerHieght=123");
          break;
        case "sj":
          this.$router.push("/nav/news-sj");
          break;
        case "jh":
          this.$router.push("/nav/news-jh");
          break;
        case "fx":
          this.$router.push("/nav/news-fx");
          break;
        case "ry":
          this.$router.push("/nav/news-ry");
          break;
        case "aj":
          this.$router.push("/nav/news-aj");
          break;
        case "sc":
          this.$router.push("/nav/news-sc");
          break;
        case "dw":
          this.$router.push("/nav/news-dw");
          break;
      }
      this.newsValue = "";
    },
  },
};
</script>

