<template>
  <header :style="{ height: `${headerHieght}px` }">
    <img class="image" src="@/assets/logo.png" alt="" />
    <div class="selected">
      <div class="selected-left">
        <span @click="goLeaderCenter">个人中心</span>
        <NewsNav />
        <GuideNav />
      </div>
      <div class="selected-right">
        <ServiceNav />
        <PublicNav />
        <span @click="$message.warning('暂未开放')">专题应用</span>
      </div>
    </div>
    <div class="setting">
      <img
        style="width: 20px; height: 20px; cursor: pointer"
        src="@/assets/数字提醒.png"
        alt=""
      />
      <img
        style="width: 20px; height: 20px; cursor: pointer"
        src="@/assets/设置.png"
        alt=""
      />
      <!-- <img
        style="width: 34px; height: 34px; cursor: pointer"
        src="@/assets/头像小.png"
        alt=""
      /> -->

      <el-dropdown>
        <span class="el-dropdown-link">
          <!-- <img
            style="
              width: 7px;
              height: 4px;
              cursor: pointer;
              transform: scale(1.5);
            "
            src="@/assets/向下.png"
            alt=""
          />
        </span> -->
          <img
            style="width: 34px; height: 34px; cursor: pointer"
            src="@/assets/头像小.png"
            alt=""
          />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            v-for="item in userList"
            :key="item.id"
            @click.native="handleClick(item)"
            :disabled="item.disabled"
          >
            <img src="@/assets/头像小.png" alt="" />
            {{ item.name }}</el-dropdown-item
          >
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </header>
</template>

<script>
import NewsNav from "@/components/navs/newscenter/NewsNav.vue";
import GuideNav from "@/components/navs/guidecenter/GuideNav.vue";
import ServiceNav from "@/components/navs/servicecenter/ServiceNav.vue";
import PublicNav from "@/components/navs/publiccenter/PublicNav.vue";

export default {
  props: {
    headerHieght: {
      type: Number,
      default: 120,
    },
  },
  data() {
    return {
      value: "",
      userList: [
        {
          id: 1,
          user: "UserLeader",
          name: "指挥人员",
          disabled: false,
        },
        {
          id: 2,
          user: "UserService",
          name: "勤务人员",
          disabled: false,
        },
        {
          id: 3,
          user: "UserWork",
          name: "情报人员",
          disabled: false,
        },
        {
          id: 4,
          user: "UserGuide",
          name: "舆情人员",
          disabled: false,
        },
        {
          id: 5,
          user: "Exit",
          name: "退出",
          disabled: false,
        },
      ],
    };
  },
  mounted() {},
  components: {
    NewsNav,
    GuideNav,
    ServiceNav,
    PublicNav,
  },
  methods: {
    goLeaderCenter() {
      if (this.$route.path.includes("UserLeader")) {
        return this.$message.warning("已在当前页");
      }
      this.$router.push({
        path: `/user/UserLeader`,
      });
    },
    handleClick(item) {
      if (item.user === "Exit") {
        this.$router.push("/");
        return;
      }
      if (this.$route.path.includes(item.user)) {
        return this.$message.warning("已在当前页");
      }
      this.$router.push({
        path: `/user/${item.user}`,
      });
    },
  },
};
</script>

<style scoped lang="scss">
header {
  width: 100%;
  height: 120px;
  background: #0e2b51;
  overflow: hidden;
  background: url("@/assets/Group 10 Copy.png") no-repeat;
  background-position: center;
  position: relative;
  .image {
    position: absolute;
    left: 43%;
    top: 10px;
  }
  .selected {
    width: 80%;
    height: 39px;
    margin: 40px auto 0;

    .selected-left {
      width: 548px;
      float: left;
      margin-left: 32px;
      span {
        margin-right: 25px;
        box-shadow: 1px 1px 6px #000;
      }
      ::v-deep .el-input__inner {
        opacity: 0.8;
        background: linear-gradient(
          1deg,
          rgba(19, 135, 226, 0) 27%,
          #073f6c 100%
        );
        background-position: center;
        text-align: center;
        text-shadow: 0 0 16px #4277eb;
        font-weight: 500;
        width: 155px;
        height: 39px;
        opacity: 0.75;
        border: none;
        color: #fff !important;
        border-radius: 0;
        margin-right: 25px;
        box-shadow: 1px 1px 6px #000;
        font-size: 15px;
      }
      ::v-deep .el-input__inner:hover {
        display: inline-block;
        color: #fff !important;
        background: linear-gradient(0deg, rgba(8, 61, 104, 0) 0%, #2ca2ff 100%);
      }
      ::v-deep .el-input__suffix {
        display: none;
      }
      ::v-deep .el-input__inner::placeholder {
        color: #fff;
        text-align: center;
      }
    }
    .selected-right {
      width: 575px;
      float: right;
      margin-right: -50px;
      span {
        margin-right: 25px;
        box-shadow: 1px 1px 6px #000;
      }
      ::v-deep .el-input__inner {
        opacity: 0.8;
        background: linear-gradient(
          1deg,
          rgba(19, 135, 226, 0) 27%,
          #073f6c 100%
        );
        background-position: center;
        text-align: center;
        text-shadow: 0 0 16px #4277eb;
        font-weight: 500;
        width: 150px;
        height: 39px;
        opacity: 0.75;
        border: none;
        color: #fff !important;
        border-radius: 0;
        margin-right: 25px;
        box-shadow: 1px 1px 6px #000;
        font-size: 15px;
      }
      ::v-deep .el-input__inner:hover {
        display: inline-block;
        color: #fff !important;
        background: linear-gradient(0deg, rgba(8, 61, 104, 0) 0%, #2ca2ff 100%);
      }
      ::v-deep .el-input__suffix {
        display: none;
      }
      ::v-deep .el-input__inner::placeholder {
        color: #fff;
        text-align: center;
      }
    }
    span {
      display: inline-block;
      border: none;
      cursor: pointer;
      font-family: AlibabaPuHuiTiM;
      color: #f4f7f7;
      letter-spacing: 0;
      text-align: center;
      text-shadow: 0 0 16px #4277eb;
      font-weight: 500;
      width: 155px;
      height: 39px;
      opacity: 0.75;
      line-height: 39px;
      background-image: linear-gradient(#0e3561, #11264d);
      background-position: center;
      box-shadow: 1px 1px 6px #000;
      font-size: 15px;
    }
    span:hover {
      display: inline-block;
      color: #fff;
      background: linear-gradient(0deg, rgba(8, 61, 104, 0) 0%, #2ca2ff 100%);
    }
  }
  .setting {
    position: absolute;
    display: flex;
    width: 200px;
    height: 50px;

    top: 0;
    right: 0;
    align-items: center;
    justify-content: space-around;
  }
}
.el-dropdown-menu {
  border: none;
  background: #0e2046;
}
::v-deep .el-dropdown-menu__item {
  display: flex;
  align-items: center;
  font-size: 12px;
  justify-content: space-between;
  color: #fff;
  img {
    width: 21px;
    margin-right: 10px;
  }
}
</style>