<template>
  <el-select
    v-model="publicValue"
    placeholder="舆情中心"
    @change="handlePubllicChange"
  >
    <el-option
      v-for="item in publicList"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    >
    </el-option>
  </el-select>
</template>

<script>
export default {
  data() {
    return {
      publicValue: "舆情中心",
      publicList: [
        {
          value: "yq",
          label: "舆情态势",
        },
        {
          value: "zh",
          label: "网络账户监控",
        },
        {
          value: "sj",
          label: "网络事件报告",
        },
        {
          value: "jk",
          label: "重大事件监控",
        },
        {
          value: "fx",
          label: "热点事件发现",
        },
        {
          value: "yd",
          label: "舆情引导",
        },
      ],
    };
  },
  methods: {
    handlePubllicChange(item) {
      if (this.$route.path.includes(item)) {
        this.publicValue = "舆情中心";

        return this.$message.warning("已在当前页");
      }
      switch (item) {
        case "yq":
          this.$router.push("/nav/public-yq");
          break;
        case "zh":
          this.$router.push("/nav/public-zh");
          break;
        case "sj":
          this.$router.push("/nav/public-sj");
          break;
        case "jk":
          this.$router.push("/nav/public-jk");
          break;
        case "fx":
          this.$router.push("/nav/public-fx");
          break;
        case "yd":
          return this.$message.warning("暂未开放");
          break;
      }
      this.publicValue = "";
    },
  },
};
</script>

